<template>
    <div class="disban-main">
        <div class="d-main">
           <div class="dmain-flex">
                <label for="stuClass"></label>
                <select disabled="disabled" name="sGroup" id="stuClass">
                    <option value="">171</option>
                    <option value="">171</option>
                    <option value="">171</option>
                    <option value="">171</option>
                </select>
                <label for="stuGroup"></label>
                <select name="sGroup" id="stuGroup">
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
                <button class="btn btn-info"></button>
                <button class="btn btn-info"></button>
                <button class="btn btn-danger" data-toggle="modal" data-target="#deleGroup"></button>
           </div>
            <hr>
            <table class="table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, i) in stu" :key="i">
                        <td>{{item.sid}}</td>
                        <td>{{item.sname}}</td>
                        <td>{{item.class}}</td>
                        <td>{{item.Lv|lvTrans}}</td>
                        <td>
                            <button @click.prevent="changeId(i)" class="btn btn-warning" data-toggle="modal" data-target="#kickStu"></button>&nbsp;
                            <button @click.prevent="changeId(i)" type="button" class="btn btn-success" data-toggle="modal" data-target="#stuPwd"></button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div class="btn-group btnCount" role="group" aria-label="...">
                <button v-if="pno-1>0" type="button" class="btn btn-info"></button>
                <button v-if="pno-2>0" type="button" class="btn btn-info">{{pno-2}}</button>
                <button v-if="pno-1>0" type="button" class="btn btn-info">{{pno-1}}</button>
                <button type="button" class="btn btn-info active">{{pno}}</button>
                <button v-if="pno+1<=pageCount" type="button" class="btn btn-info">{{pno+1}}</button>
                <button v-if="pno+2<=pageCount" type="button" class="btn btn-info">{{pno+2}}</button>
                <button v-if="pno+1<=pageCount" type="button" class="btn btn-info"></button>
            </div>

            <!--  -->
            <div class="modal fade" id="stuPwd" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3>{{stu[index].sname}}</h3>
                            <hr>
                            <p><input disabled="disabled" type="text" :value="stu[index].pwd"></p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                        </div>
                   </div>
               </div>
           </div>
           <!--  -->
            <div class="modal fade" id="kickStu" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3>"{{stu[index].sid}}""{{stu[index].sname}}"</h3>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button type="button" class="btn btn-danger"></button>
                        </div>
                   </div>
               </div>
            </div>
            <!--  -->
            <div class="modal fade" id="deleGroup" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3></h3>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button type="button" class="btn btn-danger"></button>
                        </div>
                   </div>
               </div>
            </div>
       
       
        </div>
    </div>
</template>
<script>
    import "../assets/css/AdminGroup.css"
    import "../assets/css/bootstrap.css"
    import "../apiFilter/myFilter.js"

    export default {
        data(){
            return{
                index:0,
                pageCount:10,
                pno:2,
                stu:[
                    {sid:"01",sname:"1",Lv:"1",class:"171",pwd:"123a"},
                    {sid:"02",sname:"2",Lv:"1",class:"171",pwd:"123b"},
                    {sid:"03",sname:"3",Lv:"1",class:"171",pwd:"123c"},
                    {sid:"04",sname:"4",Lv:"1",class:"171",pwd:"123d"},
                    {sid:"06",sname:"6",Lv:"1",class:"171",pwd:"123e"},
                    {sid:"05",sname:"5",Lv:"1",class:"171",pwd:"123f"},
                    {sid:"07",sname:"7",Lv:"1",class:"171",pwd:"123g"},
                    {sid:"08",sname:"8",Lv:"1",class:"171",pwd:"123h"},
                    {sid:"09",sname:"9",Lv:"1",class:"171",pwd:"123i"},
                    {sid:"10",sname:"10",Lv:"1",class:"171",pwd:"123j"},
                ]
             }
        },
        methods:{
            changeId(i){
                this.index = i;
            }
        }        
    }
</script>