<template>
    <div class="kick-main">
           <div class="kick-search">
                <div class="kick-input-search">
                        <input @blur="blurColor" @focus="focusColor" type="text">
                        <input @blur="blurColor" @focus="focusColor" type="text">
                        <input @blur="blurColor" @focus="focusColor" type="text">
                        <input @blur="blurColor" @focus="focusColor" type="text">
                    <button class="btn btn-info"></button>
                </div>
                <div class="kick-table">
                 <table class="table">
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, i) in stu" :key="i">
                                <td>{{item.sid}}</td>
                                <td>{{item.sname}}</td>
                                <td>{{item.class}}</td>
                                <td>{{item.Lv|lvTrans}}</td>
                                <td>{{item.grop}}</td>
                                <td class="btnModal">
                                    <button @click.prevent="ChangeIndex(i)" class="btn btn-danger" data-toggle="modal" data-target="#myModal1"></button>&nbsp;
                                    <button @click.prevent="ChangeIndex(i)" class="btn btn-success" data-toggle="modal" data-target="#myModal2"></button>&nbsp;
                                    <button @click.prevent="ChangeIndex(i)" class="btn btn-warning" data-toggle="modal" data-target="#myModal3"></button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div class="btn-group btnCount" role="group">
                        <button v-if="pno-1>0" type="button" class="btn btn-info"></button>
                        <button v-if="pno-2>0" type="button" class="btn btn-info">{{pno-2}}</button>
                        <button v-if="pno-1>0" type="button" class="btn btn-info">{{pno-1}}</button>
                        <button type="button" class="btn btn-info active">{{pno}}</button>
                        <button v-if="pno+1<=pageCount" type="button" class="btn btn-info">{{pno+1}}</button>
                        <button v-if="pno+2<=pageCount" type="button" class="btn btn-info">{{pno+2}}</button>
                        <button v-if="pno+1<=pageCount" type="button" class="btn btn-info"></button>
                    </div>
                </div>
            </div>

            <!--  -->
            <div class="modal fade" id="myModal1" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3>{{stu[index].sname}}</h3>
                            <hr>
                            <p><input disabled="disabled" type="text" :value="stu[index].sid"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].grop"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].class"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].Lv"></p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button class="btn btn-danger"></button>
                        </div>
                   </div>
               </div>
           </div>

            <!--  -->
           <div class="modal fade" id="myModal2" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3>{{stu[index].sname}}</h3>
                            <hr>
                            <p><input disabled="disabled" type="text" :value="stu[index].sid"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].grop"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].class"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].Lv"></p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button class="btn btn-info"></button>
                        </div>
                   </div>
               </div>
           </div>

            <!--  -->
            <div class="modal fade" id="myModal3" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3>{{stu[index].sname}}</h3>
                            <hr>
                            <p><input disabled="disabled" type="text" :value="stu[index].sid"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].grop"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].class"></p>
                            <p><input disabled="disabled" type="text" :value="stu[index].Lv"></p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button class="btn btn-warning"></button>
                        </div>
                   </div>
               </div>
           </div>
    </div>
</template>
<script>
import "../assets/css/AdminStudent.css";
import "../assets/css/bootstrap.css";
import "../apiFilter/myFilter.js"

export default {
    data(){
        return{
            index:0,
            pageCount:10,
            pno:1,
            stu:[
                {sid:"01",sname:"1",grop:"",Lv:"1",class:"171"},
                {sid:"02",sname:"2",grop:"",Lv:"1",class:"171"},
                {sid:"03",sname:"3",grop:"",Lv:"1",class:"171"},
                {sid:"04",sname:"4",grop:"",Lv:"1",class:"171"},
                {sid:"05",sname:"5",grop:"",Lv:"1",class:"171"},
                {sid:"06",sname:"6",grop:"",Lv:"1",class:"171"},
                {sid:"07",sname:"7",grop:"",Lv:"1",class:"171"},
                {sid:"08",sname:"8",grop:"",Lv:"1",class:"171"},
                {sid:"09",sname:"9",grop:"",Lv:"1",class:"171"},
                {sid:"10",sname:"10",grop:"",Lv:"1",class:"171"},
            ]
        }
    },
    methods:{
        ChangeIndex(i){
            this.index = i;
        },
        focusColor(e) {
            $(e.target).css("background-color", "#82929d");
            $(e.target).css("color","#fff");
        },
        blurColor(e) {
            $(e.target).css("background-color", "#fff");
            $(e.target).css("color","#000");
        },
        defaultFocus() {
            $(".login-box>input[name='username']").focus();
        },
    }
};
</script>